	module.exports = {"data":{"markdownRemark":{"html":"<h2>Float Property</h2>\n<p>The <code>float</code> property allows an element to float to the left or right of another element. A primary use of <code>float</code> is to have an image on the left or right of a block of text.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.my-element</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">float</span><span class=\"token punctuation\">:</span> left<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">float</span><span class=\"token punctuation\">:</span> right<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">float</span><span class=\"token punctuation\">:</span> inline-start<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">float</span><span class=\"token punctuation\">:</span> inline-end<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">float</span><span class=\"token punctuation\">:</span> none<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/CSS/float\">MDN Documentation</a></p>","fields":{"slug":"/css/properties/float-property/"},"frontmatter":{"title":"Float Property"}}},"pathContext":{"slug":"/css/properties/float-property/"}}

